body{
  background: #000000;
}

.grid{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 15px;
}

.box{
  width: auto;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.icon{
  font-size: 35px;
  color: #000000;
}

@keyframes buttonClick{
  from{
    background: #ffffff;
  }to{
    background: #F3B63A;
  }
}

@keyframes buttonHover{
  0%
  {
    background: #FAD02E;
  }
  50%
  {
    background: #BB2CD9;
  }
  100%
  {
    background: #FAD02E;
  }

}

@keyframes blobAnimation1
{
  0%{
    height: 30px;
    width: 30px;
    background: #FFF222;
  }
  50%
  {
    height: 70px;
    width: 70px;
    background: #ffffff;
  }
  100%
  {
    height: 30px;
    width: 30px;
    background: #FFF222;
  }
}


Footer{
  bottom: 0px;
  left: 0px;
}

.footer{
  bottom: 0px;
  left: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 4rem;
}

@keyframes blobAnimation2
{
  0%{
    height: 50px;
    width: 50px;
    background: #FFF222;
  }
  50%
  {
    height: 100px;
    width: 100px;
    background: #ffffff;
  }
  100%
  {
    height: 50px;
    width: 50px;
    background: #FFF222;
  }
}


.box:hover{
  animation-name: buttonHover;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in;
}

.box:active{
  animation-name: buttonClick;
  animation-duration: 1s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
}

.blob1
{
  
  border-radius: 200px;
  background: #ffffff;
  align-self: left;

  animation-name: blobAnimation1;
  animation-duration: 6s;
  animation-timing-function: ease-in;
  animation-iteration-count: infinite;
  opacity: 0.4;
}


.blob2
{
  
  border-radius: 200px;
  background: #ffffff;
  align-self: left;

  animation-name: blobAnimation2;
  animation-duration: 6s;
  animation-timing-function: ease-in;
  animation-iteration-count: infinite;
  opacity: 0.6;
}

